<template>
    <div style="display: flex;flex-direction: column; align-items: center;margin-top: 100px">
        <el-card style="width: 640px">
          <div>账号安全</div>
        <el-form ref="accountFormRef" :model="accountForm" label-width="80px"  style="margin-left: 100px; width: 400px"  label-suffix=":">
          <el-form-item label="手机号" >
            <el-input v-model="accountForm.mobile" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="登陆密码" >
            <el-input v-model="accountForm.password" :disabled="pwddisable">
              <el-button slot="append" @click="changePasswordVisible=true">修改</el-button>
            </el-input>
          </el-form-item>
          <el-form-item label="邮箱">
            <el-input v-model="accountForm.mail" :disabled="true"></el-input>
          </el-form-item>
        </el-form>
        </el-card>
      <el-button @click="goLoginOut">退出登陆</el-button>
      <el-button @click="goAdmin">去管理员界面</el-button>

      <el-dialog
        title="修改密码"
        :visible.sync="changePasswordVisible"
        width="40%"
      >
        <el-form :model="password">

          <el-form-item label="验证码">
            <el-input v-model="password.code">
              <el-button slot="append">获取验证码</el-button>
            </el-input>
          </el-form-item >
          <el-form-item label="密码">
            <el-input  v-model="password.password"></el-input>
          </el-form-item>
          <el-form-item  label="密码确认">
            <el-input v-model="password.checkPassword"></el-input>
          </el-form-item>
          <el-button type="primary">确定</el-button>

        </el-form>

      </el-dialog>

    </div>
</template>

<script>
    export default {
        name: "Account",
      data(){
          return{
        accountForm:{
            mobile:'18722648040',
            password:'123456',
            mail:'1535045887@qq.com'
        },
            password:{
              code:'',
              password:'',
              checkPassword:''
            },
          pwddisable:true,
            changePasswordVisible:false,
          }
      },
      methods:{
        changePassword(){

        },
        goLoginOut(){
          this.$router.push('/login')
        },
        goAdmin(){
          this.$router.push('/admin')
        }
      },
      mounted() {
      }
    }
</script>

<style scoped>

</style>



