<template>
<el-row class="adm-container">
  <el-col :span="17">
    <el-image :src="imgSrc" ></el-image>
  </el-col>
  <el-col :span="7" class="login-box">
    <div>帧秒分后台</div>
          <el-form
        ref="LoginFormRef"
        :model="loginForm"
        label-width="0"
        :rules="LoginFormRules"
        class="login-form"
      >
            <el-form-item prop="mobileNum">
              <el-input
                v-model="loginForm.mobileNum"
                prefix-icon="el-icon-phone"
              ></el-input>
            </el-form-item>
            <el-form-item prop="password">
              <el-input
                v-model="loginForm.password"
                prefix-icon="el-icon-lock"
                type="password"
              ></el-input>
            </el-form-item>
            <el-form-item class="btns">
              <el-button type="primary" @click="login">登录</el-button>
            </el-form-item>


      </el-form>
  </el-col>
</el-row>
</template>

<script>
    export default {
        name: "AdminLogin",
      data(){
          return {
            imgSrc:"../../../static/images/people-in-couch-1024248.jpg",
                loginForm: {
          mobileNum: "18502616338",
          password: "123",
          checkcode: "god's code"
        },
                  LoginFormRules: {
          mobileNum: [
            { required: true, message: "请输入用户名", trigger: "blur" }
          ],
          password: [
            { required: true, message: "请输入密码", trigger: "blur" },
            {
              min: 3,
              max: 15,
              message: "密码长度在6 到 15 个字符",
              trigger: "blur"
            }
          ]
        }
          }
      }
    }
</script>

<style scoped>
.adm-container{
  width:100vw;
  height:100vh;
  overflow: hidden;
}
.el-image{
  height:100%;
}
.login-box{
  height: 100%;
  display:flex;
  flex-direction: column;
  align-items: center;
  margin-top:15%
}
</style>



