<template>
  <el-container>
    <span class="header-title" @click="goHome">帧秒分</span>
    <el-container>
      <el-aside width="200px" style=" margin-top: 70px;">
        <el-card>
          <div class="btn-group">
            <el-button >  账号中心</el-button>
            <el-button type="text" @click="goToUserInfo">个人信息</el-button>
            <el-button type="text"  @click="goToMessage">消息中心</el-button>
            <el-button type="text" @click="goToMyMeeting" >我的会议</el-button>
            <el-button type="text" @click="goToMyVideo" >我的审阅</el-button>
            <el-button type="text" @click="goToAccount" >账号安全</el-button>

          </div>
        </el-card>
      </el-aside>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
  export default {
    name: "User",
    data() {
      return {
        userInfo: {},
        disabled: true,
        fileList: [
          {
            name: "food.jpeg",
            url:
              "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100"
          }
        ]
      };
    },
    methods: {
      goHome(){
        this.$router.push('/')
      },
      goToUserInfo(){
        this.$router.push('/user/userInfo')
      },
      goToMessage(){
        this.$router.push('/user/message')
      },
      goToMyVideo(){
        this.$router.push('/user/myVideo')
      },
      goToAccount(){
        this.$router.push('/user/account')
      },
      goToMyMeeting(){
        this.$router.push('/user/myMeeting')
      }
    },
    mounted() {

    }
  };
</script>

<style scoped>
  .header-title {
    display: flex;
    justify-content: center;
    line-height: 60px;
    font-size: 24px;
    background: linear-gradient(to right, red, blue);
    -webkit-background-clip: text;
    color: transparent;
    -webkit-text-fill-color: transparent;
    text-fill-color: transparent;
    cursor: pointer;
  }
  .btn-group{
    height: 82vh;
    display: flex;
    flex-direction: column;

  }
</style>



