<template>
  <div>
    <el-header style="position: relative">
      <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
        <el-tab-pane label="全部会议" name="first">
          <div class="meeting-list">
            <div
              v-for="meeting in meetingList"
              :key="meeting.meetingId"
              class="meeting-item"
            >
              <el-card>
                <el-form :model="meeting">
                  <el-form-item label="title">
                    <span>{{ meeting.title }}</span>
                  </el-form-item>
                  <el-form-item label="ownerName">
                    <span>{{ meeting.ownerName }}</span>
                  </el-form-item>
                  <el-form-item label="startTime">
                    <span>{{ meeting.startTime }}</span>
                  </el-form-item>
                  <el-form-item label="endTime">
                    <span>{{ meeting.endTime }}</span>
                  </el-form-item>
                  <el-form-item label="note">
                    <span>{{ meeting.note }}</span>
                  </el-form-item>
                </el-form>
              </el-card>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="待办列表" name="todo">  <div
          v-for="meeting in meetingList"
          :key="meeting.meetingId"
          class="meeting-item"
        >
          <el-card>
            <el-form :model="meeting">
              <el-form-item label="title">
                <span>{{ meeting.title }}</span>
              </el-form-item>
              <el-form-item label="ownerName">
                <span>{{ meeting.ownerName }}</span>
              </el-form-item>
              <el-form-item label="startTime">
                <span>{{ meeting.startTime }}</span>
              </el-form-item>
              <el-form-item label="endTime">
                <span>{{ meeting.endTime }}</span>
              </el-form-item>
              <el-form-item label="note">
                <span>{{ meeting.note }}</span>
              </el-form-item>
            </el-form>
          </el-card>
        </div></el-tab-pane>
        <el-tab-pane label="我创建的" name="mine">  <div
          v-for="meeting in meetingList"
          :key="meeting.meetingId"
          class="meeting-item"
        >
          <el-card>
            <el-form :model="meeting">
              <el-form-item label="title">
                <span>{{ meeting.title }}</span>
              </el-form-item>
              <el-form-item label="ownerName">
                <span>{{ meeting.ownerName }}</span>
              </el-form-item>
              <el-form-item label="startTime">
                <span>{{ meeting.startTime }}</span>
              </el-form-item>
              <el-form-item label="endTime">
                <span>{{ meeting.endTime }}</span>
              </el-form-item>
              <el-form-item label="note">
                <span>{{ meeting.note }}</span>
              </el-form-item>
            </el-form>
          </el-card>
        </div></el-tab-pane>
        <el-tab-pane label="待办列表" name="history">  <div
          v-for="meeting in meetingList"
          :key="meeting.meetingId"
          class="meeting-item"
        >
          <el-card>
            <el-form :model="meeting">
              <el-form-item label="title">
                <span>{{ meeting.title }}</span>
              </el-form-item>
              <el-form-item label="ownerName">
                <span>{{ meeting.ownerName }}</span>
              </el-form-item>
              <el-form-item label="startTime">
                <span>{{ meeting.startTime }}</span>
              </el-form-item>
              <el-form-item label="endTime">
                <span>{{ meeting.endTime }}</span>
              </el-form-item>
              <el-form-item label="note">
                <span>{{ meeting.note }}</span>
              </el-form-item>
            </el-form>
          </el-card>
        </div></el-tab-pane>
      </el-tabs>

      <el-input
        placeholder="查找会议"
        v-model="meetingInput"
        prefix-icon="el-icon-search"
        class="search-meeting"
      >
        <el-button slot="append" @click="searchMeeting">搜索</el-button>
      </el-input>
      <el-button
        type="text"
        @click="showCreateMeetingVisible = true"
        class="create-meeting"
        >创建会议</el-button
      >
    </el-header>
    <el-dialog
      title="创建会议"
      :visible.sync="showCreateMeetingVisible"
      width="40%"
      @close="CreateMeetingClose"
    >
      <el-form :model="meeting">
        <el-form-item label="title">
          <el-input v-model="meeting.title"></el-input>
        </el-form-item>
        <el-form-item label="belongTo projectId">
          <el-input v-model="meeting.belongTo" disabled></el-input>
        </el-form-item>
        <el-form-item label="startTime">
          <el-input v-model="meeting.startTime"></el-input>
        </el-form-item>
        <el-form-item label="endTime">
          <el-input v-model="meeting.endTime"></el-input>
        </el-form-item>
        <el-form-item label="note">
          <el-input v-model="meeting.note"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="createMeeting">创建会议</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "Meeting",
  data() {
    return {
      meetingList: [],
      activeName: "first",
      meetingInput: "",
      projectId:'',
      showCreateMeetingVisible: false,
      meeting: {
        title:'',
        belongTo: '',
        startTime:'',
        endTime:'',
        note:''
      },

    };
  },
  methods: {
    CreateMeetingClose() {
      this.meeting = {};
    },
    async createMeeting() {
      const { data: res } = await this.$http.post("meeting/", this.meeting);
      if (res.result === 1) {
        this.$message({
          message: "成功创建会议",
          type: "success"
        });
      } else {
        this.$message({
          message: res.message,
          type: "error"
        });
      }
      this.showCreateMeetingVisible = false;
    },
    async searchMeeting() {
      const { data: res } = await this.$http
        .post(`meeting/search/`, {
          linkNum: this.meetingInput
        })
        .catch(function(error) {
          console.log(error);
        });

      if (res.result == 1) {
        this.meetingList=res.data.meetingList
        console.log(res);
      } else {
        this.$message({
          message: res.message,
          type: "error"
        });
      }
    },
    async getMeetingByName(name) {
        switch(name){
          case 'todo': this.getTodoMeeting(); break;
          case 'mine': this.getMineMeeting(); break;
          case 'history':this.getHistoryMeeting();break;
        }
    },
    async getTodoMeeting(){
      const { data: res } = await this.$http
        .get(`meeting/todo`)
        .catch(error => console.log(error));
      if (res.result == 1) {
        this.meetingList=res.data.meetingList
      } else {
        this.$message({
          message: res.message,
          type: "error"
        });
      }
    },
    async getHistoryMeeting(){
      const { data: res } = await this.$http
        .get(`meeting/history`)
        .catch(error => console.log(error));
      if (res.result == 1) {
        this.meetingList=res.data
      } else {
        this.$message({
          message: res.message,
          type: "error"
        });
      }
    },
    async getMineMeeting(){
      const { data: res } = await this.$http
        .get(`meeting/mine`)
        .catch(error => console.log(error));
      if (res.result == 1) {
        this.meetingList=res.data.meetingList
      } else {
        this.$message({
          message: res.message,
          type: "error"
        });
      }
    },
    handleClick(tab, event) {
      if (tab.name != "first") {
        this.getMeetingByName(tab.name);
      }
    },
    async getMeeting(project) {
      const { data: res } = await this.$http
        .get(`project/${project}/getMeeting`)
        .catch(function(error) {
          console.log(error);
        });
      if (res.result == 1) {
        this.$message({
          message: "获取会议列表",
          type: "success"
        });
        this.meetingList = res.data;
      } else {
        this.$message({
          message: res.message,
          type: "error"
        });
      }
    }
  },
  mounted() {
    this.token = window.sessionStorage.getItem("token");
    this.projectId = this.$route.params.id;
    this.meeting.belongTo = this.$route.params.id
    console.log(this.projectId);
    this.getMeeting(this.projectId);
  }
};
</script>

<style scoped>
.meeting-list {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 25%);
  grid-template-rows: repeat(auto-fill, 200px);
}
.search-meeting {
  width: 300px;
  position: absolute;
  top: 2px;
  left: 450px;
}
.create-meeting {
  position: absolute;
  left: 780px;
  top: 2px;
}
</style>



